import React, { useEffect } from 'react'

//Composant affichant un texte animÃ© sur la page d'accueil
const TexteAccueil = () => {
    
     useEffect(() => {
         let array = ["tournois", "events", "rencontres", "plaisir"];
         let wordIndex = 0;
         let letterIndex=0;

         const target = document.getElementById("text-target");

         //Animation de chaque lettre des mots
         const createLetter = () => {
             const letter = document.createElement("span");
             target.appendChild(letter);

             letter.classList.add("letter");
             letter.style.opacity = "0";
             letter.style.animation = "anim 5s ease forwards";
             letter.textContent = array[wordIndex][letterIndex];

             setTimeout(() => {
                 letter.remove();
             },2000);
         };

         //boucle d'animation des mots
         const loop = () => {
             setTimeout(() => {
                 if(wordIndex >= array.length) {
                     wordIndex=0;
                     letterIndex=0;
                     loop();
                 } else if(letterIndex<array[wordIndex].length) {
                     createLetter();
                     letterIndex++;
                     loop();
                 } else {
                     letterIndex=0;
                     wordIndex++;
                     setTimeout(() => {
                         loop();
                     }, 2000);
                 }
             }, 80);
     };
     loop();
}, []);
    
    return (
        <span className="dynamic-text">
            <span className="simply">Association 2021-2022</span>
            <span id="text-target"></span>
        </span>
    );
};

export default TexteAccueil;